html,
body {
    position: relative;
    width: 100%;
    min-height: 400px;
}

input[type="checkbox"] {
    display: none;
}

label {
    position: relative;
    display: inline-block;
    z-index: 1;
    border: 1px solid #b8b8b8;
    margin-left: 10px;
    border-radius: 1px;
    width: 12px;
    height: 12px;
    cursor: pointer;
}

label.mark {
    background: url("../img/mark1.png") no-repeat -1px -1px;
}

a:hover {
    color: #ff873e;
    text-decoration: underline;
}

.cartMain {
    position: relative;
    width: 100%;
    margin: 0 auto;
    padding: 0px 0px 100px;
    height: auto;
    box-sizing: border-box
}

/*购物车头部*/

.cartMain_hd {
    width: 100%;
    height: 50px;
    line-height: 50px;
    color: #3c3c3c;
}

.cartMain_hd .cartTop {
    height: 50px;
}

.cartMain_hd .cartTop .list_chk {
    width: 80px;
    text-indent: 30px;
}

.cartMain_hd .cartTop .list_con {
    width: 5.7rem;
}

.cartMain_hd .cartTop .list_chk label {
    position: absolute;
    left: 10px;
    top: 20px;
    margin: 0;
}

.cartMain_hd .cartTop .list_info {
    padding: 0;
    text-indent: 15px;
}

.cartMain_hd .cartTop .list_con {
    text-indent: 140px;
}

.cartBox {
    width: 100%;
    margin-bottom: 15px;
}

.cartBox .shop_info {
    position: relative;
    width: 100%;
    height: 38px;
    background-color: #fff;
    line-height: 38px;
    vertical-align: baseline;
}

.cartBox .shop_info .all_check {
    position: relative;
    float: left;
    width: 30px;
    height: 38px;
}

.cartBox .shop_info .all_check input[type="checkbox"] {
    position: absolute;
    z-index: 0;
    left: -20px;
    top: -20px;
}

.cartBox .shop_info .all_check .shop {
    position: absolute;
    top: 13px;
}

.cartBox .shop_info .shop_name {
    float: left;
}

/*商品列表*/

.cartBox .order_content {
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    width: 100%;
}

.cartBox .order_content a {
    display: block;
}

.order_lists {
    width: 100%;
    height: 82px;
    border-bottom: 1px solid #e7e7e7;
}

.order_lists:last-child {
    border-bottom: none;
}

.order_lists li {
    float: left;
    height: 100%;
}

.order_lists .list_chk {
    width: 30px;
    position: relative;
}

.order_lists .list_chk input[type="checkbox"] {
    position: absolute;
    z-index: 0;
    left: -20px;
    top: -20px;
}

.order_lists .list_chk label {
    margin: 30px 0 0 10px;
}

.order_lists .list_con {
    width: auto;
    padding-left: 0;
}

.order_lists .list_con .list_img {
    width: 1rem;
    height: auto;
    object-fit: cover;
    margin-top: 20px;
    float: left;
}

.order_lists .list_con .list_img img {
    width: 100%;
    vertical-align: top;
}

.order_lists .list_con .list_text {
    margin: 30px 0 0 9px;
    line-height: 18px;
    width: 1.3rem;
    float: left;
    font-size: .3rem;
}

.order_lists .list_con .list_text a {
    color: #3c3c3c;
}

.order_lists .list_con .list_text a:hover {
    color: #ff873e;
    text-decoration: underline;
}

.order_lists .list_info {
    width: 252px;
    box-sizing: border-box;
    padding: 20px 0;
}

.order_lists .list_info p {
    color: #9c9c9c;
    line-height: 18px;
    margin-left: 15px;
}

.order_lists .list_price {
    width: auto;
    padding-right: .4rem;
}

.order_lists .list_price .price {
    margin-top: 30px;
    line-height: 18px;
    font-family: Verdana, Tahoma, arial;
    color: #3c3c3c;
    font-weight: bold;
    font-size: .3rem;
}

.order_lists .list_amount {
    width: auto;
}

.order_lists .list_amount .amount_box {
    margin-top: 30px;
    width: 77px;
    height: 25px;
    position: relative;
}

.order_lists .list_amount .amount_box input {
    width: 30px;
    height: 23px;
    line-height: 15px;
    border: 1px solid #aaa;
    color: #343434;
    text-align: center;
    padding: 4px 0;
    background-color: #fff;
    z-index: 2;
    position: absolute;
    left: 18px;
    float: left;
}

.order_lists .list_amount .amount_box a {
    float: left;
    height: 23px;
    width: 17px;
    border: 1px solid #e5e5e5;
    background: #f0f0f0;
    text-align: center;
    line-height: 23px;
    color: #444;
    position: absolute;
    top: 0;
}

.order_lists .list_amount .amount_box a:hover {
    border-color: #ff873e;
    text-decoration: none;
    color: #ff873e;
    z-index: 3;
}

.order_lists .list_amount .amount_box .reduce {
    left: 0;
}

.order_lists .list_amount .amount_box .reSty {
    color: #cbcbcb;
}

.order_lists .list_amount .amount_box .reSty:hover {
    border-right: none;
    border-color: #e5e5e5;
    text-decoration: none;
    color: #cbcbcb;
}

.order_lists .list_amount .amount_box .plus {
    border-left-color: transparent;
    right: 0;
    left: 50px;
}

.order_lists .list_sum {
    text-align: center;
    width: 4%;
}

.order_lists .list_sum .sum_price {
    line-height: 18px;
    margin-top: 30px;
    font-size: .3rem;
    font-family: Verdana, Tahoma, arial;
    color: #ff994d;
    font-weight: bold;
} // .order_lists .list_op {
//     width: auto;
// }
.order_lists .list_op .del {
    margin-top: 30px;
    font-size: .25rem;
    line-height: 18px;
    margin-left: 20px;
}

/*底部总计算价*/

.bar-wrapper {
    width: 100%;
    height: 50px;
    position: fixed;
    bottom: -1px;
    z-index: 99;
    opacity: 0.95;
    background: #333333;
}

.bar-wrapper .bar-right {
    float: right;
    color: #ccc;
}

.bar-wrapper .bar-right strong {
    color: #ffcf61;
}

.bar-wrapper .bar-right .piece {
    float: left;
    min-width: 110px;
    margin-right: 20px;
    height: 50px;
    line-height: 50px;
}

.bar-wrapper .bar-right .piece .piece_num {
    display: inline-block;
    padding: 0 10px;
    font-weight: 700;
    font-size: .45rem;
    font-family: tohoma, arial;
}

.bar-wrapper .bar-right .totalMoney {
    float: left;
    min-width: 90px;
    height: 50px;
    line-height: 50px;
}

.bar-wrapper .bar-right .totalMoney .total_text {
    float: right;
    font-weight: 400;
    font-size: .45rem;
    font-family: Arial;
    vertical-align: middle;
    margin-right: 10px;
    margin-left: 8px;
}

.bar-wrapper .bar-right .calBtn {
    float: left;
}

.bar-wrapper .bar-right .calBtn a {
    display: block;
    width: 70px;
    height: 50px;
    color: #ccc;
    background: rgba(0, 0, 0, .3);
    cursor: not-allowed;
    font-size: .35rem;
    letter-spacing: 5px;
    text-decoration: none;
    line-height: 50px;
    text-align: center;
    border-radius: 2px;
}

.bar-wrapper .bar-right .calBtn a.btn_sty {
    background: #25a385;
    cursor: pointer;
    color: #fff;
}

/*自己定义的模态框*/

.model_bg {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    opacity: .9;
    background: rgba(0, 0, 0, .6);
    z-index: 999;
    display: none;
}

.my_model {
    position: fixed;
    display: none;
    top: 50%;
    left: 50%;
    margin-top: -50px;
    margin-left: -200px;
    z-index: 9999;
    width: 360px;
    height: 120px;
    border: 1px solid #aeaeae;
    border-radius: 3px;
    padding: 20px;
    background: #fff;
}

.my_model .title {
    font-size: 14px;
    color: #3c3c3c;
    font-weight: 700;
    margin-bottom: 20px;
}

.my_model .title .closeModel {
    float: right;
    cursor: pointer;
}

.my_model p {
    line-height: 16px;
}

.my_model .opBtn {
    margin-top: 20px;
}

.my_model .opBtn a {
    width: 58px;
    height: 28px;
    line-height: 28px;
    text-align: center;
    -webkit-border-radius: 1px;
    -moz-border-radius: 1px;
    -ms-border-radius: 1px;
    border-radius: 1px;
    display: inline-block;
    margin-right: 10px;
    font-weight: 700;
}

.my_model .dialog-sure {
    background: #52a0e5;
    color: #fff;
    border: 1px solid #52a0e5;
}

.my_model .dialog-close {
    background: #fff;
    border: 1px solid #d9d9d9;
    color: #3c3c3c;
}

.allChoose {
    position: absolute;
}

@media screen and (min-width:1024px) {
    .cartBox {
        width: 80%;
        margin: 0 auto;
        margin-bottom: 15px;
        border-left: 1px solid #d9d9d9;
        border-right: 1px solid #d9d9d9;
    }
    .cartMain_hd {
        width: 80%;
        height: 50px;
        line-height: 50px;
        color: #3c3c3c;
        margin: 0 auto;
    }
}